@use '../../base/mixins' as m;
@use '../../base/variables' as v;


.testimoniales-blog {
    
    @include m.desktop {
        @include m.grid(3, 4rem); 
    }


    &__blog {
        @include m.desktop {
            grid-column: 2 / 4;
        }
    }

    &__heading {
        @include m.heading(true); 
    }
}

.testimonial {
    text-align: center;

    &__imagen {
        width: 20rem;
        margin: 0 auto;
    }

    &__contenido {
        background-color: v.$blanco;
        margin-top: -10rem;
        padding: 10rem 2rem 2rem 2rem;
    }

    &__nombre {
        font-size: 2.4rem;
        font-weight: bold;
    }

    &__texto {
        &::before {
            content: '';
            background-image: url(../img/quote.svg);
            background-repeat: no-repeat;
            background-position: center;
            display: inline-block;
            width: 4rem;
            height: 4rem;
            margin-right: 1rem;
        }
    }
}

.entrada {
    background-color: v.$blanco;
    margin-bottom: 2rem;


    @include m.tablet {
        display: grid;
        grid-template-columns: 2fr 3fr;
        align-items: center;    
    }

    &__imagen-contenedor {
        height: 100%;
    }

    &__imagen {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }

    &__contenido {
        padding: 5rem 2rem;
    }

    &__meta {
        font-size: 1.4rem;
    }

    &__enlace {
        @include m.boton(v.$primario, v.$blanco); 
        display: block;

        @include m.tablet {
            display: inline-block;
        }
    }

}